body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.layout-sidebar {
  min-height: 100vh;
}
.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
.content-container {
  min-height: 80vh;
}

.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
}
.login-form {
  border-radius: 18px;
  padding: 18px;
  background: #fff;
}
.menu-icon {
  color: #fff;
  margin: 8px 0px 0px 16px;
  font-size: 24px;
}
.min-meta {
  height: 74px;
}
.align-right {
  text-align: right;
}
.page-header {
  background: transparent;
}
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.user-modal .ant-modal-body {
  height: 80vh;
  overflow: auto;
}

.user-list {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  background: #fff;
  border-radius: 5px;
  margin-bottom: 5px;
  padding-left: 10px;
  padding-top: 10px;
}

.user-list:hover {
  background: #f8f9fb;
}

/* Booking widget css */
.calendar-section {
  padding: 24px;
  align-items: stretch;
  background: #fff;
  border: 1px solid var(--text-color-level3);
  border-radius: 8px;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1),
    0 1px 10px 0 rgba(52, 58, 68, 0.12);
  width: 100%;
}

.calendar-details-section {
  padding: 24px;
}

.calendar-book-section {
  padding: 18px;
}
.bookwidget .ant-fullcalendar-tbody .ant-fullcalendar-value {
  font-weight: bold;
  color: #1790ff;
}
.bookwidget
  .ant-fullcalendar-tbody
  .ant-fullcalendar-disabled-cell
  .ant-fullcalendar-value {
  color: rgba(0, 0, 0, 0.25);
}
.bookwidget .ant-fullcalendar-selected-day .ant-fullcalendar-value{
  color: #fff;
}
.bookwidget .ant-fullcalendar-value {
  border-radius: 4px;
  margin-right: auto;
  margin-left: auto;
  display: block;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  line-height: 34px;
}

.bookwidget .ant-fullcalendar-header .ant-radio-button-wrapper {
  display: none;
}
.bookwidget {
  border-radius: 8px;
  background: #fff;
  min-height: 85vh;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1),
    0 1px 10px 0 rgba(52, 58, 68, 0.12);
}
.time-slot-radiogroup {
  width: 100%;
  padding: 10px;
  height: 500px;
  overflow: auto;
}
.time-slot-radio {
  display: block;
  margin-top: 10px;
  border-radius: 5px;
  text-align: center;
  border: 1px solid #1790ff !important;
  height: 52px !important;
  line-height: 50px !important;
}

.time-slot-radio.ant-radio-button-wrapper:not(:first-child)::before {
  background-color: transparent;
}
@media (max-width: 600px) {
  .time-slot-radiogroup {
    width: 100%;
    padding: 10px;
    height: 100%;
    overflow: auto;
  }
  .calendar-details-section {
    border-right: 0px;
    border-bottom: 1px solid #c7c4c4;
  }
  .bookwidget {
    min-height: 100vh;
    box-shadow: 0 0 0 0;
  }
}
